import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import * as $ from 'jquery';
import { ValidationModel } from '../../models/validation.class';
import { LoginService } from './../../services/LoginService';
import { AddchemistService } from '../../services/AddChemistService';

@Component({
  selector: 'app-activation',
  templateUrl: './activation.component.html',
  styleUrls: ['./activation.component.scss'],
  providers: [AddchemistService]
})
export class ActivationComponent implements OnInit {
  public remember_Me_Value: Boolean = false;

  public Username: string = "";
  public Password: string = "";
  public UserId: any;
  constructor(public _addChemistService: AddchemistService, public router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    $('#loaderDiv').show();
    $('#activationDiv').hide();
    $('#loaderIncorrectDiv').hide();

    let guid = this.route.snapshot.queryParams["guid"];

    this._addChemistService.getUserByGUID(guid).subscribe(response => {
    if(response.data == null  || response.data == undefined){
      $('#loaderDiv').hide();
      $('#loaderIncorrectDiv').show();
    }else{
      this.UserId = response.data.Id;
      $('#activationDiv').show();
      $('#loaderIncorrectDiv').hide();
      $('#loaderDiv').hide();
    }

    });



    

  }



  rememberMe() {
    this.remember_Me_Value = true;
  }// End of Remember Me Function

  public runner: boolean = false;

  activate() {
    if (this.runner == false) {
      $('.error').hide();
      $('#loader').show();
      this.runner = true;
      console.log(ValidationModel.validateString(this.Username));
      if (ValidationModel.validateString(this.Username)) {
        this.runner = false;
        $('#username').show();
        return;
      }
      if (ValidationModel.validateString(this.Password)) {
        this.runner = false;
        $('#password').show();
        return;
      }

      var body = {
        UserId :this.UserId,
        UserName :this.Username,
        Password : this.Password

      }

      this._addChemistService.addUserAfterAuth(body).subscribe(response => {
        console.log(response);
        if(response.code == 200){
          this.router.navigateByUrl('');
        }
      });
      
    }
  }
}



